// Place dedicated for global styles
html,
body {
    height: 100%;
    width: 100%;
}

* {
    box-sizing: border-box;
}

html,
body {
    font-size: 16px;
    @media (min-width: $tablet) and (max-width: $tablet-landscape) {
        font-size: 15px;
    }
    @media (min-width: $phone-big) and (max-width: $tablet) {
        font-size: 14px;
    }
    @media (min-width: $phone) and (max-width: $phone-big) {
        font-size: 13px;
    }
    @media (min-width: $phone) and (max-width: $tablet) {
        font-size: 12px;
    }
    @media (max-width: $phone) {
        font-size: 11.5px;
    }
    @media (max-width: $phone-small) {
        font-size: 9.5px;
    }
}

p,
h1,
h2,
h3,
span {
    color: $font-color;
    font-family: $lato-font;
}

h1 {
    font-family: $exo-font;
}

button {
    font-family: $lato-font;
    font-weight: 800;
}

html,
body,
.container {
    height: 100%;
    width: 100%;
    margin: 0;
}

.shadow-box {
    overflow: hidden;
    box-shadow: 0 0 32px rgba(0, 0, 0, 0.5);
}